<template>
	<div class="case-wrapper">
		<h1 class="case-title">
			<i class="material-icons" @click.prevent="$router.go(-1)">
				keyboard_backspace
			</i>

			<span class="text">{{ currentCase.title }}</span>

			<i
				class="material-icons"
				v-if="currentCase.link"
				:title="currentCase.link"
				@click.prevent="redirect(currentCase.link)"
			>
				open_in_new
			</i>
		</h1>
		<p class="case-description">{{ currentCase.text }}</p>

		<img
			class="case-img"
			:src="`../img/portfolio/${currentCase.slug}/main.png`"
			:alt="currentCase.slug"
			ondragstart="return false"
		/>

		<div class="case-block">
			<h2>Project objective</h2>
			<p>{{ currentCase.objective }}</p>
		</div>

		<div class="case-block">
			<h2>Screens</h2>
			<div class="screens">
				<CoolLightBox
					:items="caseImages"
					:index="currentScreen"
					@close="currentScreen = null"
					:useZoomBar="true"
					:fullScreen="true"
				>
				</CoolLightBox>

				<img
					class="case-screen"
					v-for="(image, i) of caseImages"
					:key="i"
					:src="image"
					:alt="`screen_${i}`"
					@click="currentScreen = i"
					ondragstart="return false"
				/>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	beforeRouteEnter(to, from, next) {
		next();
		document.querySelector("#app").classList.add("toCase");
	},
	beforeRouteLeave(to, from, next) {
		document.querySelector("#app").classList.remove("toCase");
		document.querySelector("#app").classList.add("fromCase");
		setTimeout(() => {
			document.querySelector("#app").classList.remove("fromCase");
			next();
		}, 280);
	},
	mounted() {
		window.scrollTo(0, 0);
		document.getElementsByTagName("body")[0].classList = this.currentCase.slug;
	},
	destroyed() {
		document.getElementsByTagName("body")[0].removeAttribute("class");
	},
	data() {
		return {
			currentScreen: null
		};
	},
	methods: {
		redirect(link) {
			window.open(link);
		}
	},
	computed: {
		currentCase() {
			const pathSlug = this.$router.currentRoute.params.slug;
			return this.$store.state.cases.find(c => c.slug === pathSlug);
		},
		caseImages() {
			let images = [];

			for (let i = 1; i <= this.currentCase.maxScreens; i++) {
				try {
					images.push(
						require(`../../public/img/portfolio/${this.currentCase.slug}/screen_${i}.png`)
					);
				} catch (e) {
					console.warn(e.message);
				}
			}

			return images;
		}
	}
};
</script>

