			margin: 0 1.25rem 0 1.75rem
	&-block
		margin-top: $m * 2
	&-description
		font-size: 1.25rem
	&-img
		margin-top: $m

.screens
	+row-flex()
	justify-content: space-around
	.case-screen
		+col()
		+size(6)
		+size-lg(12)
		margin-top: $m
		cursor: pointer
		transition: .3s all
		&:hover
			transform: scale(1.025)

